body {
  margin: 0;
  padding: 5px;
  font-family: Helvetica, Arial, sans-serif;
  background-color: #fdfdfd;
}

* {
  color: #222;
}

/* FLEx */
.flex {
  display: -ms-flexbox;
  display: flex;
}
.horizontal, .horizontal-only {
  -ms-flex-direction: row;
      flex-direction: row;
}
.vertical {
  -ms-flex-direction: column;
      flex-direction: column;
}
@media screen and (max-width: 720px){
  .horizontal {
    -ms-flex-direction: column;
        flex-direction: column;
  }
}
.flex-center {
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.flex-1 {
  -ms-flex: 1 1;
      flex: 1 1;
}
.flex-2 {
  -ms-flex: 2 1;
      flex: 2 1;
}
.flex-3 {
  -ms-flex: 3 1;
      flex: 3 1;
}
.flex-4 {
  -ms-flex: 4 1;
      flex: 4 1;
}

/* BUTTONS */
.btn {
  border: 1px solid #999;
  border-radius: 10px;
  padding: 5px 15px;
  margin: 5px;
  font-size: 1rem;
  cursor: pointer;
  height: 30px;
}
.btn:hover {
  background-color: rgba(0,0,0,0.30);
}
.btn-blue {
  border: 1px solid transparent;
  background-color: rgb(9, 119, 167);
  color: #fefefe;
  cursor: pointer;
}
.btn-blue:hover {
  background-color: rgb(3, 62, 87);

}
.btn-red {
  border: 1px solid transparent;
  background-color: rgb(202, 0, 0);
  color: #fefefe;
  cursor: pointer;
}
.btn-red:hover {
  background-color: rgb(151, 6, 6);
}
.btn-green {
  border: 1px solid transparent;
  background-color: rgb(17, 174, 11);
  color: #fefefe;
  cursor: pointer;
}
.btn-green:hover {
  background-color: rgb(10, 102, 7);
}

/* INPUTS */
.form-group {
  margin: 5px;
}
.form-group > label {
  display: block;
  font-size: 0.9rem;
}
.form-group > .form-control {
  border: 1px solid #bbb;
  border-radius: 5px;
  padding: 5px 10px;
  margin: 5px 0;
  font-size: 1rem;
  width: 70%;
}

/* WIDTH */
.width-90 {
  width: 90%;
}

.container {
  padding: 5px;
  margin: 5px;
}

/* TEXT */
.anchor {
  text-decoration: underline;
  color: #127ac5;
  font-size: 1rem;
}

.alert {
  border: 1px solid #999;
  border-radius: 10px;
  padding: 15px;
  font-size: 0.9rem;
  margin: 5px;
  background-color: #ccc;
  color: #272727;
}

.alert-danger {
  border: 1px solid rgb(112, 6, 6);
  background-color: rgb(255, 159, 159);
  color: #550505;
}


